<template>
  <div class="col-lg-8" style="width: 500px">
    <v-row>
      <v-col class="d-flex justify-center align-center mb-6">
        <strong class="text-h3">Sign Up</strong>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-text-field
          label="Username"
          prepend-inner-icon="mdi-account-outline"
          v-model="username"
        ></v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-text-field
          label="Email"
          prepend-inner-icon="mdi-email-outline"
          v-model="email"
        ></v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-text-field
          label="password"
          :prepend-inner-icon="
            password ? 'mdi-lock-open-outline' : 'mdi-lock-outline'
          "
          :append-icon="
            showPassword ? 'mdi-eye-outline' : 'mdi-eye-off-outline'
          "
          :type="showPassword ? 'text' : 'password'"
          @click:append="showPassword = !showPassword"
          v-model="password"
        ></v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-btn block color="success" @click="createAccount"> Sign Up </v-btn>
      </v-col>
    </v-row>
    
    <v-dialog v-model="dialog" width="500">
      <!-- <template v-slot:activator="{ on, attrs }">
        <v-btn color="red lighten-2" dark v-bind="attrs" v-on="on">
          Click Me
        </v-btn>
      </template> -->

      <v-card>
        <v-card-title class="headline red darken-4" primary-title>
          <span class="white--text">注册失败</span>
        </v-card-title>

        <v-card-text class="py-4">
          {{ errorInfo }}
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false"> 确定 </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import AccountService from "@/service/account_service";
export default {
  name: "account-signup", // AccountLogin
  // 箭头函数形式的data
  data: () => ({
    showPassword: false, // 用于是否显示密码
    username: "", // 用于输入用户名或Email
    password: "", // 用于输入密码
    email: "", // 用于电子邮箱
    dialog: false, // 是否显示对话框
    errorInfo: "", // 错误信息
    message: { 202: "用户名已被注册", 203: "邮箱已被注册", "-1": "无法连接到服务器" },
  }),
  methods: {
    createAccount() {
      AccountService.signUp(this.username, this.password, this.email)
        .then((user) => {
          console.log(user);
          this.$router.push("/");
        })
        .catch((error) => {
          this.dialog = true;
          this.errorInfo = this.message[error.code];
          console.log("注册失败：", error.code);
        });
    },
  },
};
</script>

